<template>
  <div id="bg-body">
    <div class="bg bg-1 d-md-none"></div>
    <div class="bg bg-2 d-none d-md-block"></div>
    <div class="bg bg-3"></div>
  </div>
</template>
<script>
export default {
  mounted: function () {
    this.chengBg();
  },
  methods: {
    chengBg: function () {
      let home = document.querySelectorAll("#home section");
      let bgr = document.querySelectorAll("#bg-body .bg");
      bgr[2].style.cssText = `display: none;`;
      window.onscroll = function () {
        if (window.scrollY >= home[0].offsetTop) {
          bgr[2].style.cssText = `display: block;`;
          bgr[1].style.cssText = `opacity: 0;`;
          bgr[0].style.cssText = `opacity: 0;`;
        } else {
          bgr[2].style.cssText = `display: none;`;
          bgr[1].style.cssText = `opacity: 1;`;
          bgr[0].style.cssText = `opacity: 1;`;
        }
      };
    },
  },

  name: "bg-body",
};
</script>
<style lang="scss" scoped>
.bg {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -99;
  // opacity: 0;
}
.bg-1 {
  background: url("../assets/lading-sm.jpg");
  background-size: 100% 100%;
  background-position: 0% 30px;
}
.bg-2 {
  background: url("../assets/lading-lg.png");
  background-size: 100% 100%;
  background-position: 0% 60px;
}
.bg-3 {
  background-image: url("../assets/bg-body-4.jpg");
  background-size: cover;
  opacity: 0.6;
}
</style>
