<template>
  <div id="app">
    <AppHeader />
    <router-view />
    <Footer />
  </div>
</template>

<script>
import AppHeader from "@/components/globale/AppHeader.vue";
import Footer from "@/components/globale/footer.vue";

export default {
  name: "app",
  components: {
    AppHeader,
    Footer,
  },
  mounted: function () {
    this.scroltop();
  },
  methods: {
    scroltop: function () {
      let myBtnScroll = document.querySelector(".scrolToTop");

      myBtnScroll.onclick = function () {
        window.scrollTo({
          top: 0,
          behavior: "smooth",
        });
      };

      window.onscroll = function () {
        window.scrollY >= 500
          ? (myBtnScroll.style.cssText = `right: 20px;`)
          : (myBtnScroll.style.cssText = `right: -200px;`);
      };
    },
  },
};
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
