<template>
  <!--   -->
  <nav class="bgGradient navbar navbar-expand-md w-100 navbar-dark">
    <div class="container container-fluid">
      <div class="navbar-brand">
        <img class="" src="../../assets/logo.png" />
      </div>

      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNavAltMarkup"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div class="navbar-nav ms-auto">
          <router-link class="nav-link" to="/" exact>Home</router-link>
          <router-link class="nav-link" to="/about">About Us</router-link>
          <router-link class="nav-link" to="/Menu">Our Menu</router-link>
          <router-link class="nav-link" to="/Gallery" replace
            >Gallery</router-link
          >
        </div>
      </div>
    </div>
  </nav>
</template>
<script>
export default {
  mounted: function () {
    this.addClass();
  },
  methods: {
    addClass() {
      let lastScrol = 0;
      let nav = document.querySelector("nav");

      window.addEventListener("scroll", function () {
        let currentScroll = window.pageYOffset;

        if (
          currentScroll > lastScrol + 5 &&
          !nav.classList.contains("scrol-down")
        ) {
          nav.classList.add("scrol-down");
        }
        if (
          currentScroll <= lastScrol &&
          nav.classList.contains("scrol-down")
        ) {
          nav.classList.remove("scrol-down");
        }
        lastScrol = currentScroll;
      });
    },
  },

  name: "appHeader",
};
</script>

<style scoped lang="scss">
.navbar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99999;
  padding: 0;
  transition: all 1s ease;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -ms-transition: all 1s ease;
  -o-transition: all 1s ease;
  box-shadow: 0px 6px 10px 0px black;
}
.navbar-brand {
  width: 120px;

  img {
    max-width: 100%;
  }
}

a {
  color: black;
  border-radius: 10px;
  margin: 5px 10px;
  box-shadow: 0px 0px 10px 0px black;
  &:hover {
    transform: translateY(-5px);
    transition: all 0.5s ease;
  }
}
a.router-link-exact-active {
  color: #f1c606 !important;
}

.scrol-down {
  transform: translateY(-110%);
  -webkit-transform: translateY(-110%);
  -moz-transform: translateY(-110%);
  -ms-transform: translateY(-110%);
  -o-transform: translateY(-110%);
}
</style>
