<template>
  <section id="Contact">
    <h2 class="title">contact us</h2>
    <div class="container">
      <div class="row">
        <div class="col-md-6">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3447.7226935922445!2d-9.369315015119208!3d30.216461181820037!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0xdb3c3a1c76a13e7%3A0x98e5f99c919bc39f!2sSnack%20Favory!5e0!3m2!1sar!2sma!4v1640029945486!5m2!1sar!2sma"
            style="border: 0"
            allowfullscreen=""
            loading="lazy"
          ></iframe>
        </div>
        <form action="" class="col-md-6 pt-3">
          <input
            class="w-75 p-2 input"
            type="text"
            placeholder="Your Name"
            name="name"
            v-model="namevalue"
          />

          <div v-if="eroors.nameErors" class="error h6">
            {{ eroors.nameErors }}
          </div>

          <input
            class="w-75 p-2 input"
            type="text"
            placeholder="Your Phone"
            name="mobile"
            v-model="mobilevalue"
          />
          <div v-if="eroors.mobileErors" class="error h6">
            {{ eroors.mobileErors }}
          </div>
          <input
            class="w-75 p-2 input"
            type="email"
            placeholder="Your Email"
            name="email"
            v-model="emailvalue"
          />
          <div v-if="eroors.emailErors" class="error h6">
            {{ eroors.emailErors }}
          </div>
          <div class="textarea w-75">
            <textarea
              class="w-100 p-2 input"
              name="message"
              placeholder="Tell Us About Your Needs"
              :maxlength="maxCharacter"
              v-model="messagevalue"
              @input="conteCharacter"
            ></textarea>
            <span class="maxlength"
              >{{ remainingCharacter }}/{{ maxCharacter }}</span
            >
          </div>
          <div v-if="eroors.messageErors" class="error h6">
            {{ eroors.messageErors }}
          </div>

          <input
            type="submit"
            value="Send"
            class="fs-5 fw-bold w-50 p-2 bg-warning submit"
            @click="validatForm"
          />
        </form>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      namevalue: null,
      mobilevalue: null,
      emailvalue: null,
      messagevalue: null,
      maxCharacter: 400,
      remainingCharacter: 400,
      eroors: {
        nameErors: null,
        mobileErors: null,
        emailErors: null,
        messageErors: null,
      },
    };
  },

  methods: {
    conteCharacter: function () {
      this.remainingCharacter = this.maxCharacter - this.messagevalue.length;
    },

    validatForm: function (ele) {
      let removeSpaces = function (inputsValues, filterValue) {
        if (inputsValues)
          inputsValues.split(" ").forEach((vul) => {
            if (vul) {
              filterValue.push(vul);
            }
          });
        return filterValue;
      };

      this.namevalue = removeSpaces(this.namevalue, []).join(" ");
      this.mobilevalue = removeSpaces(this.mobilevalue, [])
        .join("")
        .replace("+", "00");
      this.emailvalue = removeSpaces(this.emailvalue, []).join("");
      this.messagevalue = removeSpaces(this.messagevalue, []).join(" ");

      let filters = function (vl) {
        return vl.split("").filter((e) => {
          return !isNaN(parseInt(e));
        });
      };

      !this.namevalue
        ? (this.eroors.nameErors = "Name Cant Be Empty")
        : filters(this.namevalue).length > 0
        ? (this.eroors.nameErors = "Name Cant Be Numbers")
        : (this.eroors.nameErors = null);

      !this.mobilevalue
        ? (this.eroors.mobileErors = "Mobile Cant Be Empty")
        : filters(this.mobilevalue).length != this.mobilevalue.length
        ? (this.eroors.mobileErors = "Mobile Cant Be Letters")
        : (this.eroors.mobileErors = null);

      !this.emailvalue
        ? (this.eroors.emailErors = "email Cant Be Empty")
        : (this.eroors.emailErors = null);

      !this.messagevalue
        ? (this.eroors.messageErors = "Message Cant Be Empty")
        : (this.eroors.messageErors = null);

      if (!Object.values(this.eroors).join("").length) {
        return true;
      }

      ele.preventDefault();
    },
  },

  name: "contact",
};
</script>
<style lang="scss" scoped>
form {
  background-color: rgba(73, 58, 58, 0.26);
  .input {
    caret-color: rgb(233, 236, 5);
    background-color: #e8ecac;
    border: none;
    outline: none;
    border-bottom: 3px solid rgb(167, 170, 5);
    margin: 15px auto;
  }
  textarea {
    height: 120px;
    resize: none;
  }
  .submit {
    border-radius: 10px;
    color: #444402;
  }
}
iframe {
  min-height: 400px;
  width: 100%;
  height: 100%;
}
.error {
  background-color: rgb(238, 137, 137);
  color: rgb(158, 29, 29);
  width: 75%;
  text-align: left;

  margin: 0 auto;
  margin-top: -10px;
  padding: 5px;
  position: relative;
  &::before {
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    background-color: inherit;
    top: -10px;
    right: 10px;
    transform: rotateZ(45deg);
  }
}
.textarea {
  margin: 0 auto;
  position: relative;
  padding-bottom: 5px;
  margin-bottom: 5px;
  span {
    position: absolute;
    right: 0;
    bottom: 0;
    color: white;
    z-index: 5;
    box-shadow: 0px 0px 7px 0px black;
  }
}
</style>
