<style lang="scss" scoped>
.carousel {
  margin: 50px auto;
  width: 100%;
  height: 85vh;
  padding-top: 5px;
}
.carousel-cell {
  margin: 2vh 0 0 35px;
  counter-increment: carousel-cell;
  width: 300px;
  height: 80vh;
  transition: all 0.3s ease;
  filter: blur(3px);

  .pag {
    transform: scale(0.8);
    transition: all 1s ease;
    box-shadow: 0px 0px 15px 0px #1a1919;
  }
}
.carousel-cell.is-selected {
  filter: blur(0);

  .pag {
    transform: scale(1);
  }
}

.pag {
  border-radius: 90% 90% 50% 50% / 10% 10% 10% 10%;
  -webkit-border-radius: 90% 90% 50% 50% / 10% 10% 10% 10%;
  -moz-border-radius: 90% 90% 50% 50% / 10% 10% 10% 10%;
  -ms-border-radius: 90% 90% 50% 50% / 10% 10% 10% 10%;
  -o-border-radius: 90% 90% 50% 50% / 10% 10% 10% 10%;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  background-image: url("../assets/menu-bg.jpg");
  background-size: 50% 50%;
  display: flex;
  justify-content: center;
  align-items: center;

  &::before {
    content: "";
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    border: 2px solid white;
    border-radius: inherit;
    -webkit-border-radius: inherit;
    -moz-border-radius: inherit;
    -ms-border-radius: inherit;
    -o-border-radius: inherit;
    position: absolute;
    top: 10px;
    left: 10px;
  }
  &::after {
    content: "";
    width: calc(100% - 40px);
    height: calc(100% - 40px);
    border: 2px solid white;
    border-radius: inherit;
    -webkit-border-radius: inherit;
    -moz-border-radius: inherit;
    -ms-border-radius: inherit;
    -o-border-radius: inherit;
    position: absolute;
    top: 20px;
    left: 20px;
  }
  .content {
    height: 83%;
    width: 76%;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    box-sizing: border-box;

    img {
      max-width: 100%;
      max-height: 100%;
      align-self: center;
    }
    .ingredints {
      font-size: 10px;
      opacity: 0.8;
    }
  }
}
.app {
  position: relative;
  height: 75vh;
  perspective: 1000px;

  .paper {
    position: absolute;
    width: 40%;
    height: 100%;
    top: 0;
    left: 10%;
    transform-origin: right center;
    transition: 1s;
    -webkit-transition: 1s;
    -moz-transition: 1s;
    -ms-transition: 1s;
    -o-transition: 1s;
    transform-style: preserve-3d;
    cursor: grab;
    .next {
      width: 40px;
      height: 40px;
      background-color: rgba(245, 180, 60, 0.521);
      font-size: 20px;
      font-weight: 900;
      padding-top: 5px;
      position: absolute;
      right: -120%;
      top: 40%;
      border-radius: 50%;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
    }

    .page-2 {
      transform: rotateY(180deg);
      -webkit-transform: rotateY(180deg);
      -moz-transform: rotateY(180deg);
      -ms-transform: rotateY(180deg);
      -o-transform: rotateY(180deg);
    }
  }

  .rotat {
    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -ms-transform: rotateY(180deg);
    -o-transform: rotateY(180deg);
  }
}

.link {
  padding: 20px;
  a {
    color: white;
    width: 200px;
    height: 200px;
    background: rgb(172, 121, 27);
    margin: 0 auto;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    position: relative;
    @media (min-width: 800px) {
      width: 250px;
      height: 250px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      border-radius: inherit;
      border: 10px solid;
    }
    &::after {
      width: calc(100% + 20px);
      height: calc(100% + 20px);
      border-color: #05fce7;
      border-left-color: transparent;
      animation: retatafter 7s linear infinite;
    }

    &::before {
      width: calc(100% + 40px);
      height: calc(100% + 40px);
      border-color: #ff03c0;
      border-right-color: transparent;
      animation: retatbefor 7s linear infinite;
    }
  }
}
@keyframes retatafter {
  to {
    transform: rotateZ(360deg);
  }
}
@keyframes retatbefor {
  to {
    transform: rotateZ(-360deg);
  }
}

.code-QR {
  padding: 20px;
  img {
    margin: 0px auto;
  }
}
</style>
