<template>
  <section id="gall">
    <div class="container">
      <sliderAnimation />
      <div class="slider-gallery row text-warning">
        <span @click="delet" class="delet"><i class="bi bi-x-lg h1"></i></span>
        <div class="app col-11 col-sm-9">
          <span
            @click="index++"
            @click.prevent="chengStyle"
            class="cheng next h1"
            ><i class="bi bi-chevron-right"></i
          ></span>
          <span
            @click="index--"
            @click.prevent="chengStyle"
            class="cheng prev h1"
            ><i class="bi bi-chevron-left"></i
          ></span>
          <div
            class="slid-img"
            v-for="img in imags"
            :key="img"
            :data-index="imags.indexOf(img)"
          >
            <img class="" :src="img" alt="" />
          </div>
        </div>
      </div>

      <span v-for="img in imags" :key="img" class="img">
        <img
          @click="addStyle"
          :data-idx="imags.indexOf(img)"
          class=""
          :src="img"
          alt=""
        />
      </span>
    </div>
  </section>
</template>
<script>
import sliderAnimation from "./sliderAnimation.vue";

let imags = [
  require("../assets/gallery-imgs/img-1.jpg"),
  require("../assets/gallery-imgs/img-2.jpg"),
  require("../assets/gallery-imgs/img-3.jpg"),
  require("../assets/gallery-imgs/img-4.jpg"),
  require("../assets/gallery-imgs/img-5.jpg"),
  require("../assets/gallery-imgs/img-6.jpg"),
  require("../assets/gallery-imgs/img-7.jpg"),
  require("../assets/gallery-imgs/img-8.jpg"),
  require("../assets/gallery-imgs/img-9.jpg"),
  require("../assets/gallery-imgs/img-10.jpg"),
  require("../assets/gallery-imgs/img-11.jpg"),
  require("../assets/gallery-imgs/img-12.jpg"),
  require("../assets/gallery-imgs/img-13.jpg"),
  require("../assets/gallery-imgs/img-14.jpg"),
  require("../assets/gallery-imgs/img-15.jpg"),
  require("../assets/gallery-imgs/img-16.jpg"),
  require("../assets/gallery-imgs/img-17.jpg"),
  require("../assets/gallery-imgs/img-18.jpg"),
  require("../assets/gallery-imgs/img-19.jpg"),
  require("../assets/gallery-imgs/img-20.jpg"),
  require("../assets/gallery-imgs/img-21.jpg"),
  require("../assets/gallery-imgs/img-22.jpg"),
  require("../assets/gallery-imgs/img-23.jpg"),
  require("../assets/gallery-imgs/img-24.jpg"),
  require("../assets/gallery-imgs/img-25.jpg"),
  require("../assets/gallery-imgs/img-26.jpg"),
  require("../assets/gallery-imgs/img-27.jpg"),
  require("../assets/gallery-imgs/img-28.jpg"),
  require("../assets/gallery-imgs/img-29.jpg"),
  require("../assets/gallery-imgs/img-30.jpg"),
  require("../assets/gallery-imgs/img-31.jpg"),
  require("../assets/gallery-imgs/img-32.jpg"),
  require("../assets/gallery-imgs/img-33.jpg"),
  require("../assets/gallery-imgs/img-34.jpg"),
  require("../assets/gallery-imgs/img-35.jpg"),
  require("../assets/gallery-imgs/img-36.jpg"),
  require("../assets/gallery-imgs/img-37.jpg"),
  require("../assets/gallery-imgs/img-38.jpg"),
  require("../assets/gallery-imgs/img-39.jpg"),
  require("../assets/gallery-imgs/img-40.jpg"),
  require("../assets/gallery-imgs/img-41.jpg"),
];

export default {
  data: function () {
    return {
      imags: imags,
      index: 0,
    };
  },

  mounted: function () {},
  methods: {
    chengStyle: function () {
      let imgs = document.querySelectorAll(".slider-gallery .slid-img");
      let btnbrev = document.querySelector(".slider-gallery .prev");
      let btnNext = document.querySelector(".slider-gallery .next");

      //  ........................................................

      this.index == 0
        ? (btnbrev.style.cssText = ` display: none`)
        : (btnbrev.style.cssText = ` display: block`);

      this.index == imgs.length - 1
        ? (btnNext.style.cssText = ` display: none`)
        : (btnNext.style.cssText = ` display: block`);

      // .........................................................................

      imgs.forEach((el) => {
        el.dataset.index == this.index
          ? (el.style.cssText = ` right: 0%`)
          : (el.style.cssText = ` right: -130%`);
      });
    },
    // ..........................................................

    addStyle: function (e) {
      let myslider = document.querySelector(".slider-gallery");
      this.index = e.target.dataset.idx;
      this.chengStyle();
      myslider.style.display = ` block`;
    },
    delet: function () {
      let myslider = document.querySelector(".slider-gallery");
      myslider.style.cssText = ` display: none`;
    },
  },
  components: {
    sliderAnimation,
    // Animation,
  },
  name: "gallerySection",
};
</script>
<style lang="scss" scoped>
.container {
  padding-top: 30px;
}
.slider-gallery {
  background-color: #000000c2;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
  display: none;

  .delet {
    display: inline-block;
    cursor: pointer;
    width: 100%;
    height: 100%;
    i {
      position: absolute;
      top: 20px;
      right: 5px;
    }
  }
  .app {
    height: 80vh;
    background-color: black;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    overflow: hidden;
    .cheng {
      cursor: pointer;
      width: 10%;
      height: 10%;
      position: absolute;
      top: 45%;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 999;
    }
    .next {
      right: 0;
    }
    .prev {
      left: 0;
    }

    .slid-img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        min-width: 50%;
        max-width: 100%;
        min-height: 60%;
        max-height: 100%;
      }
    }
  }
}

.img {
  display: inline-block;
  width: 200px;
  height: 200px;
  margin: 10px;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0px 0px 10px 0px #0c0c0a;
  border-radius: 30% / 100%;
  -webkit-border-radius: 30% / 100%;
  -moz-border-radius: 30% / 100%;
  -ms-border-radius: 30% / 100%;
  -o-border-radius: 30% / 100%;
  transition: all 0.5s ease;
  position: relative;

  &::before,
  &::after {
    content: "";
    width: 50%;
    height: 100%;
    background-color: rgba(56, 51, 51, 0.349);
    position: absolute;
    top: 0;
    transition: all 1s ease;
  }

  &::before {
    left: 0;
  }
  &::after {
    right: 0;
  }
  &:hover {
    border-radius: 5%;
    -webkit-border-radius: 5%;
    -moz-border-radius: 5%;
    -ms-border-radius: 5%;
    -o-border-radius: 5%;
    transform: scale(1.3);
    z-index: 9;

    &::before {
      left: -100%;
    }
    &::after {
      right: -100%;
    }
  }
  img {
    width: 100%;
    height: 100%;
  }
}
</style>
