<template>
  <div id="animation-slider">
    <h2 class="title">gallery</h2>
    <div class="container">
      <div class="prism">
        <div class="app">
          <div
            v-for="photo in photos"
            :key="photo"
            :class="`img img-${photos.indexOf(photo) + 1}`"
          >
            <img class="photo" :src="photo" alt="" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
let photos = [
  require("../assets/animation-imgs/photo-1.jpg"),
  require("../assets/animation-imgs/photo-2.jpg"),
  require("../assets/animation-imgs/photo-3.jpg"),
  require("../assets/animation-imgs/photo-4.jpg"),
  require("../assets/animation-imgs/photo-5.jpg"),
  require("../assets/gallery-imgs/img-1.jpg"),
  require("../assets/gallery-imgs/img-4.jpg"),
  require("../assets/gallery-imgs/img-5.jpg"),
];
export default {
  data: function () {
    return {
      photos: photos,
    };
  },
  mounted: function () {
    this.style();
  },
  methods: {
    style() {
      let myImags = document.querySelectorAll("#animation-slider .img");

      let myApp = document.querySelector("#animation-slider .app");

      let Width = myImags[0].offsetWidth;
      let rotateY = (2 * Math.PI) / myImags.length;
      let rotatApp = rotateY;
      let translateZ = Width / 2 / Math.tan(rotateY / 2);
      for (let i = 0; i < myImags.length; i++) {
        myImags[i].style.cssText = `transform: rotateY(${
          rotateY * i
        }rad) translateZ(${translateZ}px)`;
      }
      setInterval(() => {
        myApp.style.cssText = `  transform: rotateY(${rotatApp}rad)`;

        rotatApp += rotateY;
      }, 3000);
      console.log(translateZ);
    },
  },

  name: "animation-slider ",
};
</script>

<style lang="scss" scoped>
#animation-slider {
  .container {
    margin-top: 50px;
    perspective: 1000px;
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    .prism {
      transform-style: preserve-3d;
      transform: rotateX(5deg);
      width: 100%;
      height: 100%;
    }
    .app {
      transform-style: preserve-3d;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      transition: all 2s ease;
      position: relative;

      .img {
        width: 250px;
        height: 80%;
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (min-width: 768px) {
          width: 300px;
        }
        img {
          -webkit-box-reflect: below 10px
            linear-gradient(transparent, #08080834);

          max-width: 80%;
          max-height: 100%;
          border-radius: 20%;
          box-shadow: 0px 0px 10px 0px rgb(68, 65, 65);
        }
      }
    }
  }
  $ret: 360deg;
  @keyframes retet {
    to {
      transform: rotateY($ret);
    }
  }
}
</style>
