<template>
  <section id="work-timing ">
    <div class="container">
      <div class="state">
        <div class="content">
          <span class="disply h2 text-light">OPEN NOW</span>
          <span class="animatoin"></span>
        </div>
      </div>
      <div class="app row justify-content-around align-items-center">
        <div class="box-clock col">
          <div class="clock bgGradient">
            <div class="timnumber">
              <span class="numHours"></span> :
              <span class="numMinutes"></span> :
              <span class="numSeconds"></span>
            </div>
            <div class="datenumber">
              <span class="nameDay"></span> : <span class="numDay"></span> /
              <span class="numMounth"></span> /
              <span class="numYesr"></span>
            </div>
            <ul>
              <li v-for="hour in Hours" :key="hour">
                <span>{{ hour }}</span>
              </li>
            </ul>
            <div class="seconds"></div>
            <div class="minutes"></div>
            <div class="hours"></div>
          </div>
        </div>
        <div class="box-time col">
          <h4 class="text-warning display-6">
            <span><i class="bi bi-clock"></i></span>
            <span class="m-3">work timing:</span>
          </h4>

          <table class="text-light w-100">
            <tr>
              <th></th>
              <th class="border p-2">From</th>
              <th class="border p-2">To</th>
            </tr>
            <tr
              v-for="day in days"
              :key="day.id"
              class="m-3 h5"
              :class="day.dayName"
            >
              <td class="border p-2 w-50">
                {{ day.dayName }}
              </td>

              <td class="border p-2">
                {{ day.from }}
              </td>
              <td class="border p-2">
                {{ day.to }}
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>
<script>
export default {
  data() {
    return {
      days: {
        Monday: {
          dayName: "Monday",
          from: "12:00",
          to: "22:00",
        },

        Tuesday: {
          dayName: "Tuesday",
          from: "13:00",
          to: "22:00",
        },
        Wednesday: {
          dayName: "Wednesday",

          from: "12:00",
          to: "22:00",
        },
        Thusday: {
          dayName: "Thusday",
          from: "13:00",
          to: "22:00",
        },
        Friday: { dayName: "Friday", from: "12:00", to: "22:00" },
        Satrday: {
          dayName: "Satrday",

          from: "13:00",
          to: "23:59",
        },
        Sunday: {
          dayName: "Sunday",
          from: "12:00",
          to: "23:00",
        },
      },

      Hours: [
        "I",
        "II",
        "III",
        "IV",
        "V",
        "VI",
        "VII",
        "VIII",
        "IX",
        "X",
        "XI",
        "XII",
      ],
    };
  },
  mounted: function () {
    this.clock();
  },
  methods: {
    clock: function () {
      let seconds = document.querySelector(".seconds");
      let minutes = document.querySelector(".minutes");
      let hours = document.querySelector(".hours");
      let numHours = document.querySelector(".timnumber .numHours");
      let numMinutes = document.querySelector(".timnumber .numMinutes");
      let numSeconds = document.querySelector(".timnumber .numSeconds");
      let nameDay = document.querySelector(".datenumber .nameDay");
      let numDay = document.querySelector(".datenumber .numDay");
      let numMounth = document.querySelector(".datenumber .numMounth");
      let numYesr = document.querySelector(".datenumber .numYesr");
      let d = 1000 * 60 * 60 * 24;
      let h = 1000 * 60 * 60;
      let m = 1000 * 60;
      let s = 1000;
      let state = document.querySelector(".state .content .disply");

      setInterval(() => {
        let now = new Date();
        let Hours = Math.floor((now % d) / h);
        let Minutes = Math.floor((now % h) / m);
        let Seconds = Math.floor((now % m) / s);
        let todayName = now.toDateString().split(" ")[0];
        let todayNum = now.getDate();
        let MounthNum = now.getMonth() + 1;
        let YesrNum = now.getFullYear();

        numHours.textContent = Hours;
        numMinutes.innerHTML = Minutes;
        numSeconds.innerHTML = Seconds;
        nameDay.innerHTML = todayName;
        numDay.innerHTML = todayNum;
        numMounth.innerHTML = MounthNum;
        numYesr.innerHTML = YesrNum;

        seconds.style.cssText = `transform: rotateZ( ${
          (360 / 60) * Seconds
        }deg);`;
        minutes.style.cssText = `transform: rotateZ( ${
          (360 / 60) * Minutes
        }deg);`;
        hours.style.cssText = `transform: rotateZ( ${
          (360 / 12) * Hours + (360 / 12 / 60) * Minutes
        }deg)`;
        let daysName = Object.keys(this.days);
        daysName.forEach((day) => {
          if (day.startsWith(todayName)) {
            document.querySelector(
              `.${day}`
            ).style.cssText = `   background-image: linear-gradient(90deg , rgb(249, 252, 67),rgb(235, 96, 96) ,rgb(255, 174, 0));
`;

            let timeWork = document.querySelectorAll(`.${day} td`);

            if (
              Hours >= timeWork[1].textContent.split(":")[0] &&
              Hours < timeWork[2].textContent.split(":")[0]
            ) {
              state.textContent = "OPEN NOW";
            } else {
              state.textContent = "CLOSED NOW";
            }
          }
        });
      }, 1000);
    },
  },

  name: "work-timing",
};
</script>
<style lang="scss" scoped>
.box-clock {
  .clock {
    width: 300px;
    height: 300px;
    margin: 10px auto;
    position: relative;
    border: 2px #5a4722 dashed;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    box-shadow: 0px 0px 10px 3px #000000;
    overflow: hidden;
    background-image: url("../assets/clock-bg.jpg");
    background-size: 100% 110%;
    background-position: 100% 100%;
    @media (min-width: 800px) {
      width: 350px;
      height: 350px;
    }
    @media (min-width: 1000px) {
      width: 400px;
      height: 400px;
    }

    &::before {
      content: "";
      width: 20px;
      height: 20px;
      background: inherit;
      border: 3px solid #fdfcb3;
      border-radius: inherit;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 3;
    }

    .seconds,
    .minutes,
    .hours {
      background-color: #fdfcb3;
      transform-origin: bottom center;
      position: absolute;
      bottom: 50%;
      width: 3px;
      &::before {
        content: "";
        background: inherit;
        position: absolute;
      }
    }

    .seconds {
      height: 48%;
      left: calc(50% - 3px / 2);
      &::before {
        width: 100%;
        height: 15%;
        left: 0;
        top: 100%;
      }
    }

    .minutes {
      height: 40%;
      left: calc(50% - 3px / 2);
      &::before {
        width: 10px;
        height: 80%;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        border-radius: 10px;
      }
    }

    .hours {
      height: 30%;
      left: calc(50% - 3px / 2);
      &::before {
        width: 12px;
        height: 80%;
        left: 50%;
        top: 0;
        transform: translateX(-50%);
        border-radius: 10px;
      }
    }

    li {
      height: 50%;
      position: absolute;
      bottom: 50%;
      left: 50%;
      transform-origin: bottom center;
      padding-top: 10px;
      color: white;

      &::before {
        content: "";
        width: 15px;
        height: 10px;
        background: #00f73ec7;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        clip-path: polygon(0% 0%, 100% 0%, 50% 100%);
      }
    }

    @for $i from 1 through 12 {
      li:nth-child(#{$i}) {
        transform: translateX(-50%) rotateZ(calc(360deg / 60 * 5 * #{$i}));

        span {
          display: inline-block;
          transform: rotate(calc(-360deg / 12 * #{$i}));
        }
      }
    }
  }
  .timnumber,
  .datenumber {
    padding: 0 5px;
    color: #2dff04;
    background: rgba(222, 209, 135, 0.151);
    box-shadow: 0px 0px 15px 0px black;
    border-radius: 5px;
    position: absolute;
    top: 60px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 9;
  }
  .datenumber {
    top: 90px;
  }
}

.state {
  width: fit-content;
  margin: 0px auto 30px;
  box-shadow: inherit;
  padding: 10px;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px black;
  .content {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-radius: inherit;
    background-color: #272222;
    z-index: 1;
  }
  .animatoin {
    display: inline-block;
    width: 200%;
    height: 500px;
    position: absolute;
    z-index: -1;
    background-image: linear-gradient(
      50deg,
      #01ffff,
      green,
      rgb(252, 51, 51),
      #01ffff,
      #d300fd,
      yellow,
      #016fff 75% 100%
    );

    animation: retet 2s linear infinite;
  }
}
.box-time {
  box-shadow: 0px 0px 20px 0px black;
  border-radius: 20px;
  padding: 20px;
}

@keyframes retet {
  to {
    transform: rotateZ(360deg);
  }
}
</style>
